@import url('https://fonts.googleapis.com/css2?family=Nerko+One&display=swap');

.center-container {
  display: flex;
  justify-content: center; /* Centers horizontally */
  align-items: center; /* Centers vertically */
  height: 100%; /* Full viewport height to center vertically */
  width: 100%; /* Full viewport width */
  background-color: none; /* Optional background for the whole viewport */
  
}

.card-grid {
  background-color: #272640;
  font-family: "Nerko One", cursive;
  font-weight: 400;
  font-size: 5rem;
  display: grid;
  justify-items: center; /* Center the cards horizontally */
  align-items: center; /* Center the cards vertically */
  height: 75vh; /* Box height */
  width: 65vw; /* Box width */
  border-radius: 40px; /* Rounded corners */
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.3); /* Optional shadow */
  }

  .cards {
    display: grid;
    grid-template-columns: repeat(2, 1fr); /* Two equal columns */
    grid-gap: 10px; /* Spacing between cards */
    justify-items: center; /* Center the cards horizontally */
    align-items: center; /* Center the cards vertically */
    user-select: none;
  }

.introduction {
  text-align: center;
  color: #3b7c85;
  font-family: "Nerko One", cursive;
  font-weight: 400;
  font-size: 2rem;
  font-style: normal;
  user-select: none;
}

.card-wrapper {
    cursor: pointer;
  }
  
.hover-card {
    transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
    border-radius: 10px; /* Optional: Add rounded corners */
}

.hover-card:hover {
    transform: scale(1.05); /* Expand the card */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); /* Add shadow effect */
}

.centered-image {
    max-width: 100%;   /* Ensure the image doesn't overflow */
    max-height: 100%;  /* Ensure the image fits within the card */
    object-fit: contain; /* Keep aspect ratio and prevent cropping */
}

.card-titles {
  color: #3b7c85;
  font-family: "Nerko One", cursive;
  font-weight: 400;
  font-size: 3.5rem;
  font-style: normal;
}
  
.custom-button {
    background: #748d92; /* Gradient background */
    color: #2e3944;
    border: none;
    padding: 10px 20px;
    font-size: 16px;
    font-weight: bold;
    border-radius: 10px; /* Rounded corners */
    transition: all 0.3s ease;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* Slight shadow */
  }
  
  .custom-button:hover {
    background: linear-gradient(135deg, #00bfff, #1e90ff); /* Reverse gradient on hover */
    transform: translateY(-3px); /* Lift the button slightly */
    box-shadow: 0 6px 10px rgba(0, 0, 0, 0.2); /* More pronounced shadow on hover */
  }
  
  .custom-button:active {
    transform: translateY(0); /* Button presses down when clicked */
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* Revert to original shadow on click */
  }

  .no-underline {
    text-decoration: none;
    color: inherit;
  }